<template>
  <div>
    <section class="p-2 row">
      <div class="col-md-3 p-2">
        <label for="level" class="form-label">Your Level</label>
        <input type="number" v-on:input="getShopInfo(level)" minlength="2" min="2" v-model="level" class="form-control" id="level">
      </div>
      <div class="col-md-6 p-2">
        <label for="xp" class="form-label">Your XP</label>
        <input type="number" v-model="xp" class="form-control" id="xp">
      </div>
      <div class="col-md-3 p-2">
        <label for="slots" class="form-label">Number of slots</label>
        <input type="number" v-model="slots" class="form-control" id="slots">
      </div>
    </section>
    <hr>
    <section class="p-2">
      <p class="title-paragraphe">Xp ({{verifyXp()+'/'+info.xpneeded}})</p>
      <div class="progress">
        <div class="progress-bar" role="progressbar" v-bind:style="{ width: xp*100/info.xpneeded + '%' }" aria-valuenow="25" aria-valuemin="0"
             v-bind:valuemax="info.xpneeded">{{ (xp * 100 / info.xpneeded).toFixed(2) }}%
        </div>
      </div>
      <div class="d-flex justify-content-between">
        <div>0</div>
        <div>{{ info.xpneeded }}</div>
      </div>
      <div class="p-2 text-center" v-if="levelUp">
        <button @click="levelUpShop" class="btn btn-outline-dark">Level UP</button>
      </div>
    </section>


    <hr>


    <section class="row p-2">
      <div class="col-md-6 p-2">
        <button class="btn w-100 btn-success" @click="persist">Save</button>
      </div>
      <div class="col-md-6 p-2">
        <button class="btn w-100 btn-danger" @click="clearLs">Clear</button>
      </div>
    </section>
    <hr>




  </div>
</template>

<script>
import xp from '../items/merchent.json';


export default {
  name: "ShopProfil",
  data() {
    return {
      infosMerchant: xp.feed.entry,
      info: {},
      levelUp: false,

      level: 2,
      slots: 0,
      xp: 0,
      xpneeded: 0,
    }
  },
  methods: {
    getShopInfo: function (lvl){
      if (this.level >= 2 || this.level !== ''){
        const infos = this.infosMerchant[lvl];
        return this.info = {
          'level': Number(infos.gsx$merchantlevel.$t),
          'xp': Number(this.xp),
          'xpneeded': Number((infos.gsx$xpneeded.$t).replace(/[, ]+/g, "").trim()),
          'slots': Number(this.slots),
        };
      }
    },
    persist: function() {
      localStorage.infos = JSON.stringify(this.getShopInfo(this.level));
      alert("Saved");
    },
    clearLs: function() {
      this.getShopInfo(this.level);
      localStorage.removeItem("infos");
      alert("Clear");
    },
    verifyXp: function () {
      let max = this.info.xpneeded;

      if (this.xp >= max) {
        this.levelUp = true;
        this.xp = max;
      }
      else if (this.xp < max){
        this.levelUp = false;
      }
      else if (this.xp < 0){
        this.xp = 0;
      }
      return this.xp;
    },
    levelUpShop: function (){
      this.level = Number(this.level) + 1;
      this.xp = 0;
      return this.getShopInfo(this.level)
    },





  },
  mounted() {
    if (localStorage.infos){
      const ls = JSON.parse(localStorage.infos);
      this.level = ls.level;
      this.xp = ls.xp;
      this.slots = ls.slots;
    }
    this.getShopInfo(this.level)
    this.verifyXp()

  },
}
</script>

